@import "header";
@import "catalog";
@import "card";
@import "rate";
@import "nav";

html {
  font-family: "Roboto", sans-serif;
  font-size: 11px;
  line-height: 13px;
}

body {
  margin: 0;
  width: 100%;
}

.visuallyhidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
